'use client'

import { use } from 'react'
import Navbar from '@/components/Navbar'
import Footer from '@/components/Footer'
import Image from 'next/image'
import Link from 'next/link'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'

// Sample blog post data - would normally come from database
const blogPostsData: Record<string, any> = {
  'spring-planting-guide': {
    title: 'Your Complete Spring Planting Guide',
    author: 'Jane Smith',
    category: 'Gardening Tips',
    image: '/images/bg.jpeg',
    publishedAt: '2024-02-15',
    tags: ['spring', 'planting', 'vegetables'],
    content: `
# Your Complete Spring Planting Guide

Spring is the most exciting time of year for gardeners! As the soil warms up and days get longer, it's time to start planning your spring garden.

## When to Start

The key to successful spring planting is timing. Start by understanding your last frost date - this varies by location but is crucial for planning.

### Cool-Season Crops
These can be planted 4-6 weeks before your last frost date:
- Lettuce
- Spinach
- Peas
- Radishes
- Carrots

### Warm-Season Crops
Wait until after your last frost date for:
- Tomatoes
- Peppers
- Cucumbers
- Squash
- Beans

## Soil Preparation

Good soil is the foundation of a successful garden. Here's what you need to do:

1. **Test your soil pH** - Most vegetables prefer pH 6.0-7.0
2. **Add organic matter** - Compost improves soil structure
3. **Till or turn** - Loosen compacted soil for root growth

## Planting Tips

- **Seed depth**: Plant seeds at a depth of 2-3 times their diameter
- **Spacing**: Follow packet instructions to prevent overcrowding
- **Watering**: Keep soil consistently moist but not waterlogged

## Common Mistakes to Avoid

1. Planting too early
2. Overcrowding plants
3. Neglecting to harden off transplants
4. Forgetting to mulch

Happy planting!
    `,
  },
  'composting-basics': {
    title: 'Composting Basics: Turn Waste into Garden Gold',
    author: 'John Doe',
    category: 'Sustainability',
    image: '/images/bg.jpeg',
    publishedAt: '2024-02-10',
    tags: ['composting', 'sustainability', 'organic'],
    content: `
# Composting Basics: Turn Waste into Garden Gold

Composting is one of the best things you can do for your garden and the environment. It reduces waste and creates nutrient-rich soil amendment.

## What is Composting?

Composting is the natural process of decomposing organic matter into a dark, earthy-smelling material called humus.

## What Can You Compost?

### Green Materials (Nitrogen-rich)
- Fruit and vegetable scraps
- Coffee grounds
- Fresh grass clippings
- Plant trimmings

### Brown Materials (Carbon-rich)
- Dry leaves
- Straw
- Shredded paper
- Cardboard

## How to Start

1. Choose a bin or create a pile
2. Layer green and brown materials
3. Keep it moist like a wrung-out sponge
4. Turn regularly for aeration
5. Wait 3-6 months for finished compost

## Tips for Success

- Maintain a 3:1 ratio of browns to greens
- Chop materials into smaller pieces for faster decomposition
- Keep your pile between 3x3x3 feet for best results

Start composting today and watch your garden thrive!
    `,
  },
}

interface PageProps {
  params: Promise<{ slug: string }>
}

export default function BlogPostPage({ params }: PageProps) {
  const resolvedParams = use(params)
  const post = blogPostsData[resolvedParams.slug]

  if (!post) {
    return (
      <main>
        <Navbar />
        <div className="container-custom py-20 text-center">
          <h1 className="text-4xl font-bold mb-4">Post Not Found</h1>
          <Link href="/blog" className="text-secondary hover:underline">
            Back to Blog
          </Link>
        </div>
        <Footer />
      </main>
    )
  }

  return (
    <main>
      <Navbar />

      {/* Hero Section */}
      <section className="relative h-96 flex items-center justify-center">
        <div className="absolute inset-0 z-0">
          <Image
            src={post.image}
            alt={post.title}
            fill
            className="object-cover"
          />
          <div className="absolute inset-0 bg-black opacity-60"></div>
        </div>
        <div className="relative z-10 text-center text-white px-4 max-w-4xl">
          <div className="text-secondary font-semibold mb-2">{post.category}</div>
          <h1 className="text-4xl md:text-5xl font-bold mb-4">{post.title}</h1>
          <div className="flex items-center justify-center gap-4 text-sm">
            <span>By {post.author}</span>
            <span>•</span>
            <span>
              {new Date(post.publishedAt).toLocaleDateString('en-US', {
                month: 'long',
                day: 'numeric',
                year: 'numeric',
              })}
            </span>
          </div>
        </div>
      </section>

      {/* Blog Content */}
      <article className="py-16 bg-white">
        <div className="container-custom max-w-4xl">
          <div className="prose prose-lg max-w-none">
            <ReactMarkdown remarkPlugins={[remarkGfm]}>
              {post.content}
            </ReactMarkdown>
          </div>

          {/* Tags */}
          <div className="mt-12 pt-8 border-t">
            <h3 className="text-lg font-semibold mb-4">Tags:</h3>
            <div className="flex flex-wrap gap-2">
              {post.tags.map((tag: string) => (
                <span
                  key={tag}
                  className="bg-gray-100 text-gray-700 px-4 py-2 rounded-full text-sm"
                >
                  #{tag}
                </span>
              ))}
            </div>
          </div>

          {/* Back to Blog */}
          <div className="mt-12 text-center">
            <Link
              href="/blog"
              className="inline-block bg-primary text-white px-8 py-3 rounded-lg font-semibold hover:bg-opacity-90 transition-colors"
            >
              ← Back to All Posts
            </Link>
          </div>
        </div>
      </article>

      <Footer />
    </main>
  )
}
